import React, { Component } from 'react'

class List extends Component {
  render() {
    const { list, pageID } = this.props
    if (!list) return null
    return <ul className="order-tabs-tabpane">
      { list.map(item => {
        return <li>
          <p className="store-status-info">
            <span className="time">{item.createTime}</span>
            <span className="status">{`${item.orderStatus === 'INITIAL' ? '' : ''}`}</span>
          </p>
          <div className="store-details-info">
            { item.orderGoodsVOS.map(good => {
              return <img
                className="img"
                src={`http://timg.ffan.com/convert/resize/url_${good.pic}/tfs/1.png`}
              />
            })}
            <div className="store-price">
              <p className="price"><span></span>{item.formatRealPay}</p>
              <a
                className="details"
                href={`/merchant/order/${pageID}?orderNo=${item.orderNo}`}
              >
                
                <i className="iconfont icon-arrR" />
              </a>
            </div>
          </div>
        </li>
      })}
    </ul>
  }
}

export default List



