import React, { Component } from 'react'
import { Tabs } from 'antd'
import List from './List'
import fetchUtil from 'utils/fetchUtil'
import { getDomain } from 'utils/domain'
import { getCookie } from 'utils/tools'
import './H5MyOrderListContainer.scss'
const TabPane = Tabs.TabPane
class OrderList extends Component {
  constructor(props) {
    super(props)
    this.state = {
      payList: []
    }
  }

  componentDidMount() {
    document.title = ''
    const url = getDomain('/bocxapi/v1/trade/user_orders', { suffix: 'com' })
    fetchUtil.getJSON(url, {
      loginToken: getCookie('boc_loginToken'),
      orderStatus: 'INITIAL, TRADE_SUCCESS, TRADE_FINISHED'
    }).then(data => {
      this.setState({
        payList: data.orderVOS
      })
    }).catch(err => {
      console.log(err)
    })
  }

  render() {
    const { payList } = this.state
    const { pageID } = this.props.params
    const unpayList = payList && payList.filter((item) => {
      return item.orderStatus === 'INITIAL'
    })
    return <div className="order-tabs-nav-wrap">
      <Tabs defaultActiveKey="1" type="line">
        <TabPane span={12} tab="" key="1">
          <List list={payList} pageID={pageID} />
        </TabPane>
        <TabPane tab="" key="2">
          <List list={unpayList} pageID={pageID} />
        </TabPane>
      </Tabs>
    </div>
  }
}

export default OrderList



